#header-line {
    width: 35vw;
    margin-left: 0;
}

.project {
    max-width: 60vw;
}

.project h3 {
    font-weight: 700;
}

.project h5 {
    margin: 5px 0 5px 0;
    font-weight: 700;
}

.link {
    font-weight: 600;
    color: var(--black);
}

.quote {
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content:space-around;
    padding: 10px;
    border-radius: 10px;
    margin: 5px 0 5px 0;
}

.quote p{
    /* font-style:italic; */
    font-weight: 600;
    font-size: 1.2rem;
    margin-left: 20px;
}

.project-summary-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
    width: 100%;
    margin: 10vh 0 3vh 0;
}

.project-summary-container p {
    margin: 0;
}

.split-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3vw;
    width: 100%;
    justify-items: center;
}

.project-opener-split {
    margin: 5vh 0 5vh 0;
}

.project-opener-left {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
}

.project-opener-right {
    width: 100%;
    text-align: center;
    justify-content: center;
}

.center-card{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: start;
    padding: 1vh 7vw 1vh 7vw;
    margin-top: 10px;
    border-radius: 10px;
    /* box-shadow: 0px 3px 5px lightgrey; */
}

.center-card .icon {
    margin-right: 20px;
}

.project-summary-container h4 {
    margin-top: 5px;
}

.project-summary-container div {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
}

.icon {
    width: 3.5vw;
    min-width: 40px;
}

#group-mockup, #video {
    width: 100%;
    border-radius: 5px;
    margin-top: 20px;
}

.section-container {
    margin-top: 7vh;
}

.sub-container {
    margin-left: 4vw;
    margin-right: 4vw;
}

.takeaways li{
    font-size: 1rem;
}

@media screen and (max-width: 992px) {

    .project-opener-split {
        display: flex;
        flex-direction: column;
    }

    #group-mockup, #video {
        max-height: 80vh;
        width: 60vw;
        margin-top: 20px;
    }
    
}

@media screen and (max-width: 768px) {
    .project-summary-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .project-summary-container div {
        margin-bottom: 10px;
    }
    
}