#profile-img {
    width: 400px;
}

.about-container {
    margin: 0 7vw 0 7vw;
}

.about-content, .like-dislike-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
}

.about-img-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-text em {
    color: var(--red);
}

.about-text strong {
    color: var(--blue);
}

.about-text a {
    color: var(--dark-yellow);
    text-decoration-line: underline;
}

.about-list, .like-dislike-container {
    /* font-size: 1rem; */
    margin-bottom: 10px;
}

@media screen and (max-width: 1200px) {
    .about-container {
        margin: 0 2vw 0 2vw;
    }
}

@media screen and (max-width: 992px) {
    .about-content, .like-dislike-container{
        grid-template-columns: 1fr;
    }   
    
    .about-container {
        margin: 0 5px 0 5px;
    }
}

@media screen and (max-width: 768px) {
    #profile-img {
        max-width: 80vw;
    }    
}